import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { EmployeeService } from '../employee.service';
import { AppPortfolio } from '../appPortfolio';
import * as XLSX from 'xlsx'; 

@Component({
  selector: 'app-display-app-port',
  templateUrl: './display-app-port.component.html',
  styleUrls: ['./display-app-port.component.css']
})
export class DisplayAppPortComponent implements OnInit {

  appPortfolio : AppPortfolio[]; 
  constructor(private service: EmployeeService,
    private router: Router) { }
    /*name of the excel-file which will be downloaded. */ 
    fileName= 'AppPortfolio.xlsx';  

ngOnInit(): void {
  this.displayAppdetails();
}
exportexcel(): void 
    {
       /* table id is passed over here */   
       let element = document.getElementById('excel-table'); 
       const ws: XLSX.WorkSheet =XLSX.utils.table_to_sheet(element);

       /* generate workbook and add the worksheet */
       const wb: XLSX.WorkBook = XLSX.utils.book_new();
       XLSX.utils.book_append_sheet(wb, ws, 'Sheet1');

       /* save to file */
       XLSX.writeFile(wb, this.fileName);
			
    }

  

  displayAppdetails()
{
  this.service.displayApp().subscribe(
    data => {
      console.log("response received");
      this.appPortfolio=data;
    },
      error =>
       {
        console.log("exception occured");
      }
  )
}
}
