import { CreateLogin } from './login/login.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { CreateAppPortfolio } from './app-portfolio/app-portfolio.component';
import {CreateKeyInitiative} from './key-initiative/key-initiative.component';
import { DivisionComponent } from './division/division.component';
import { MenuComponent } from './menu/menu.component';
import {DisplayAppPortComponent} from './display-app-port/display-app-port.component';
import {DisplayKeyinitiativeComponent} from './display-keyinitiative/display-keyinitiative.component';
import { AuthGuard } from './auth.guard';
import { CheckComponent } from './check/check.component';

const routes: Routes = [
  { path: '', redirectTo: 'login', pathMatch: 'full' },
  { path: 'login', component: CreateLogin},
  { path: 'division', component: DivisionComponent, canActivate: [AuthGuard]},
  { path: 'appPortfolio', component: CreateAppPortfolio, canActivate: [AuthGuard]},
  { path: 'menu', component: MenuComponent, canActivate:[AuthGuard]},
  { path: 'keyInitiative', component: CreateKeyInitiative, canActivate: [AuthGuard]},
  {path: 'displayAppport',component:DisplayAppPortComponent, canActivate: [AuthGuard]},
  {path: 'displayKeyinitiative',component:DisplayKeyinitiativeComponent, canActivate: [AuthGuard]},
  {path:'ckeck',component:CheckComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }