import { Component, OnInit } from '@angular/core';
import { KeyInitiative } from '../keyInitiative';
import { Router } from '@angular/router';
import { EmployeeService } from '../employee.service';

@Component({
  selector: 'app-key-initiative',
  templateUrl: './key-initiative.component.html',
  styleUrls: ['./key-initiative.component.css']
})
export class CreateKeyInitiative implements OnInit {

  keyInitiative = new KeyInitiative(); 
  constructor(private service: EmployeeService,
    private router: Router) { }

  ngOnInit(): void {
    this.keyInitiative.division=this.service.div;
  }

  saveInitiative()
{
  this.service.getKeyinitiative(this.keyInitiative).subscribe(
    data => {
    console.log("response received");
    console.log(data);
    this.router.navigate(['/menu'])},
    error =>
     {
      console.log("exception occured");
      }
  )
}
onLogout()
{
  this.router.navigate(['/login'])
}

}
