 private baseUrl = environment.baseUrl;

  constructor(private httpClient: HttpClient,private service: EmployeeService,
    private router : Router) { }

  public loginUser(login: Object):Observable<Object> {
    return this.httpClient.post<any>(`${this.baseUrl}/login`,login);
  }

  public getDivision():Observable<Division[]>{
      return this.httpClient.get<Division[]>(`${this.baseUrl}/division`);
  }

  public getDetails():Observable<Menu[]>{
    return this.httpClient.get<Menu[]>(`${this.baseUrl}/menu`);
}
  public getAppdetails(appPortfolio: Object): Observable<Object> {
    return this.httpClient.post(`${this.baseUrl}/application`, appPortfolio );
  }
  public getKeyinitiative(keyInitiative: Object): Observable<Object> {
    return this.httpClient.post(`${this.baseUrl}/keyinitiative`, keyInitiative );
  }
 
  public displayApp():Observable<AppPortfolio[]>{
    return this.httpClient.get<AppPortfolio[]>(`${this.baseUrl}/application`);
}
  public displayInitiative():Observable<KeyInitiative[]>{
  return this.httpClient.get<KeyInitiative[]>(`${this.baseUrl}/keyinitiative`);
}
