import { Component, OnInit } from '@angular/core';
import { KeyInitiative } from '../keyInitiative';
import { Router } from '@angular/router';
import { EmployeeService } from '../employee.service';
import * as XLSX from 'xlsx'; 

@Component({
  selector: 'app-display-keyinitiative',
  templateUrl: './display-keyinitiative.component.html',
  styleUrls: ['./display-keyinitiative.component.css']
})
export class DisplayKeyinitiativeComponent implements OnInit {

  keyInitiative : KeyInitiative[]; 
  constructor(private service: EmployeeService,
    private router: Router) { }
    fileName= 'KeyInitiative.xlsx';

    exportexcel(): void 
    {
       /* table id is passed over here */   
       let element = document.getElementById('excel-table'); 
       const ws: XLSX.WorkSheet =XLSX.utils.table_to_sheet(element);

       /* generate workbook and add the worksheet */
       const wb: XLSX.WorkBook = XLSX.utils.book_new();
       XLSX.utils.book_append_sheet(wb, ws, 'Sheet1');

       /* save to file */
       XLSX.writeFile(wb, this.fileName);
			
    }

  ngOnInit(): void {
    this.displayInitiativedetails();
  }

  displayInitiativedetails()
{
  this.service.displayInitiative().subscribe(
    data => {
      console.log("response received");
      this.keyInitiative=data;
    },
      error =>
       {
        console.log("exception occured");
      }
  )
}

}
